.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  padding: 3.75em 0;
  &__inner{
    position: relative;
    display: flex;
    padding-bottom: 3.75em;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 16.75em;
    margin-right: 3.75em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__mob{
    display: none;
  }
}

.burger{
  display: none;
}

.menu{
  flex-grow: 1;
  display: flex;
  align-items: center;
  &__scroll{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;
  }
  &__items{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-grow: 1;
    margin: 0 3.75em;
  }
  &__item{
    display: flex;
    align-items: center;
    img{
      width: 1.875em;
      margin-right: .75em;
    }
    span{
      font-size: 1.25em;
    }
  }
  .city-select{
    &__title{
      font-weight: 400;
      span{
        font-size: 1.25em;
      }
    }
  }
  &__btn{
    flex-shrink: 0;
    padding: 0 4em;
  }
}

.nav{
  position: absolute;
  width: 76.25em;
  max-width: 100%;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-between;
  &__link{
    font-size:  1.25em;
    line-height: 125%;
    padding-bottom: .25em;
    border-bottom: 1px solid transparent;
    &._active,
    &:hover{
      color: #fff;
      border-color: #fff;
    }
  }
}

.header-search{
  width: 13.625em;
  flex-shrink: 0;
  position: relative;
  &__icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 3.75em;
    width: 2em;
  }
  .fg{
    input{
      padding-left: 5em;
      height: 2.65em;
      border-radius: 5em;
      color: #fff;
      &::placeholder{
        color: #fff;
      }
    }
  }
}


@media screen and (max-width: 1400px){
  .header{
    padding: 2em 0;
    &__inner{
      padding-bottom: 2em;
    }
    &__logo{
      width: 10.625em;
      margin-right: 2.5em;
    }
  }

  .menu{
    &__items{
      margin: 0 2.5em;
    }
    &__item{
      img{
        width: 1.375em;
        margin-right: .75em;
      }
      span{
        font-size: 1em;
      }
    }
    &__btn{
      padding: 0 1.5em;
      height: 2.3125em;
      span{
        font-size: 1em;
      }
    }
    .city-select{
      &__title{
        margin: 0 .75em;
        span{
          font-size: 1em;
        }
      }
      img{
        width: 1.375em;
        transform: unset;
        position: static;
      }
    }
  }

  .nav{
    width: 58.125em;
    &__link{
      font-size: 1.125em;
    }
  }

  .header-search{
    width: 7.5em;
    &__icon{
      left: 1.5em;
      width: 1.375em;
    }
    .fg{
      input{
        padding-left: 3.5em;
        font-size: 1em;
        height: 2.75em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1em 0;
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
    &__inner{
      padding: 0;
      justify-content: space-between;
    }
    &__logo{
      width: 6em;
      margin: 0;
      position: relative;
      z-index: 11;
    }
    &__mob{
      display: flex;
      align-items: center;
      position: relative;
      z-index: 11;
      .menu__item{
        img{
          width: 1.125em;
        }
        span{
          font-size: .875em;
        }
      }
    }
  }

  .burger{
    position: relative;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    margin-left: 2em;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 90%;
    }
  }

  .menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background: linear-gradient(180deg, #132119 0%, #129449 26.5%, #21b962 52.5%, #02ad4b 75.5%, #008639 100%);
    padding: 4.75em 0 0;
    z-index: 10;
    &__scroll{
      padding: 1em;
      display: flex;
      flex-direction: column;
      justify-content: unset;
      overflow-y: auto;
      height: 100%;
    }
    &__btn{
      margin: 2.5em 0 0;
      width: 100%;
      height: 3.3125em;
    }
    &__items{
      order: -1;
      flex-grow: unset;
      width: 100%;
      margin: 0;
    }
    .city-select{
      order: 2;
      padding-right: 0;
    }
    &__item{
      &._hide-mob{
        display: none;
      }
    }
  }

  .header-search{
    margin: 1.5em 0 0;
    width: 100%;
    &__icon{
      display: none;
    }
    .fg{
      input{
        height: 3em;
        padding: 0 1.5em;
      }
    }
  }

  .nav{
    position: static;
    transform: unset;
    flex-direction: column;
    align-items: center;
    margin: 2.5em 0 0;
    &__link{
      font-size: 1.25em;
      @include lastM(0 0 .75em);
    }
  }
}


