.news-main{
  padding-bottom: 10em;
  .main__img{
    img{
      max-height: unset;
    }
  }
  .main__mouse{
    margin-top: 17.5em;
  }
}

.news{
  padding-top: 0;
  &__grid{
    margin-top: 3em;
    row-gap: 2em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 3em;
  }
  &__btn{
    padding: 0 5em;
    height: 3em;
    border-radius: 1em;
  }
}

.news-card{
  &__img{
    @include imgCover(53%);
    border-radius: .75em;
  }
  &__content{
    margin-top: 1.25em;
    @include grid100gap(.5em);
  }
  &__title{
    font-size: 1.75em;
    line-height: 120%;
    letter-spacing: -0.01em;
    font-weight: 600;
    a{
      &:hover{
        color: #DEDEDE;
      }
    }
  }
  &__date{
    font-size: 1em;
    line-height: 110%;
    letter-spacing: -0.01em;
    color: $block;
  }
}

.news-item{
  padding: 3.75em 0 5em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__back,
  &__share{
    width: 2em;
    @include center;
    img{
      width: 100%;
    }
  }
  &__title{
    font-size: 1.75em;
    line-height: 120%;
    letter-spacing: -0.01em;
    font-weight: 600;
  }
  &__bottom{
    width: 69.625em;
    max-width: 100%;
    margin: 5em auto 0;
  }
  &__subtitle{
    font-size: 3em;
    line-height: 120%;
    letter-spacing: -0.01em;
    font-weight: 600;
  }
  &__subtitle2{
    border-top: 1px solid #fff;
    padding-top: 1.25em;
    margin-top: 1.25em;
    span{
      font-size: 1.5em;
      line-height: 120%;
      letter-spacing: -0.01em;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 17.75em) 16.5em;
    gap: 1.25em;
    margin-top: 3em;
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: .5em;
  }
}

.news-group{
  @include lastM(0 0 1.5em);
  &__list li,
  &__title{
    font-size: 1.5em;
    line-height: 120%;
    letter-spacing: 0.01em;
  }
  &__title{
    font-weight: 600;
  }
  &__list{
    margin-top: 1em;
    @include grid100gap(.75em);
    li{
      padding-left: 1.33em;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 1em;
        height: 1em;
        background-image: url(../img/marker2.svg);
        @include bgImgSize(100%);
      }
    }
  }
}

.feedback{
  margin-top: 3em;
  &__title{
    font-size: 3em;
    line-height: 120%;
    letter-spacing: -0.01em;
    font-weight: 600;
    a,button{
      text-decoration: underline;
      &:hover{
        color: #DEDEDE;
      }
    }
  }
  &__date,
  &__type,
  &__desc{
    font-size: 1.5em;
    line-height: 120%;
    letter-spacing: -0.01em;
  }
  &__desc{
    margin-top: .5em;
  }
  &__bottom{
    display: flex;
    margin-top: 3em;
  }
  &__date,
  &__type{
    color: #abffcb;
    margin-right: 2.5em;
  }
}


@media screen and (max-width: 1400px){
  .news-main{
    padding-bottom: 8.75em;
    .main__mouse{
      margin-top: 9.25em;
    }
  }

  .news{
    &__grid{
      margin-top: 1.5em;
      row-gap: 1.25em;
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__btn{
      padding: 0 1.5em;
      height: 2.3125em;
      border-radius: 5em;
      span{
        font-size: 1em;
      }
    }
  }

  .news-card{
    &__img{
      padding-bottom: 67%;
    }
    &__title{
      font-size: 1.25em;
    }
  }

  .news-item{
    padding-top: 3em;
  }
}


@media screen and (max-width: 992px){
  .news-main{
    padding: 9em 0;
  }

  .news{
    padding-top: 2.5em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
    }
  }

  .news-card{
    &__img{
      padding-bottom: 73%;
    }
    &__content{
      margin-top: .75em;
    }
    &__title{
      font-size: 1.125em;
    }
    &__date{
      font-size: .875em;
    }
  }

  .news-item{
    padding: 1.5em 0 2.5em;
    &__title{
      font-size: 1.5em;
      font-weight: 400;
    }
    &__back,
    &__share{
      width: 1.5em;
    }
    &__bottom{
      margin-top: 3em;
    }
    &__subtitle{
      font-size: 1.25em;
    }
    &__subtitle2{
      margin-top: .5em;
      padding-top: .5em;
      span{
        font-size: 1em;
      }
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 2.5em;
    }
    &__col2{
      display: none;
    }
  }

  .news-group{
    @include lastM(0 0 1em);
    &__title{
      font-size: 1.25em;
    }
    &__list{
      margin-top: .5em;
      gap: .5em;
      li{
        font-size: 1em;
        line-height: 133%;
      }
    }
  }

  .feedback{
    margin-top: 1em;
    &__title{
      font-size: 1.25em;
    }
    &__desc{
      font-size: 1em;
    }
    &__bottom{
      justify-content: space-between;
      margin-top: 2.5em;
    }
    &__date,
    &__type{
      font-size: 1.25em;
      margin-right: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .news{
    &__grid{
      grid-template-columns: 100%;
    }
    &__btn{
      width: 100%;
    }
  }
}
