@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    overflow: hidden;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
    position: relative;
}

.m-page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    background-image: url(../img/bg.svg);
    background-size: cover;
    color: $text;
    min-height: $vh;
    font-size: calc(1760px * (16 / 1760));
    @media screen and (max-width: 1760px){
        font-size: calc(100vw * (16 / 1760));
    }
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
        padding-right: var(--scrollWidth);
    }
}

.vacancy-page{
    background-image: url(../img/bg2.svg);
}
.history-page{
    background-image: url(../img/history-bg.svg);
}

.container {
    max-width: 1760px;
    width: 100%;
    margin: 0 auto;
    padding: 0 2.5em;
}

a {
    &:hover {
        color: $primary;
    }
}

@media screen and (max-width: 1400px) {
    .m-page {
        font-size: calc(1200px * (16 / 1200));
        @media screen and (max-width: 1200px){
            font-size: calc(100vw * (16 / 1200));
        }
    }

    .vacancy-page{
        background-image: url(../img/bg.svg);
    }

    .history-page{
        background-image: url(../img/history-bg2.svg);
    }

    .container {
        max-width: 1200px;
    }
}


@media screen and (max-width: 992px){
    .m-page{
        font-size: calc(100vw * (16 / 768));
    }

    .container{
        padding: 0 1em;
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 360));
        background-size: 1000% auto;
    }

    .vacancy-page{
        //background-image: url(../img/bg3.svg);
    }

    .why-page{
        background-image: url(../img/contacts-bg.svg);
    }
}


