.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 34em;
        max-width: 100%;
        position: relative;
        z-index: 3;
        padding: 2.5em 1.5em;
        border-radius: 1em;
        display: inline-block;
        color: $black;
        box-shadow: 2px 2px 20px 0 rgba(78, 80, 79, 0.35);
    }
    &__top{
        @include grid100gap(.5em);
        text-align: center;
    }
    .close-btn{
        display: none;
    }
    &__title{
        font-size: 2em;
        line-height: 123%;
        font-weight: 500;
    }
    .close-btn{
        position: absolute;
        top: 1em;
        right: 1em;
        width: 1.5em;
        svg{
            width: 100%;
        }
    }
}

.modal-form{
    margin-top: 1.5em;
    @include grid100gap(1em);
    &__btn{
        margin-top: .5em;
        height: 3.5em;
        border-radius: 1em;
    }
    &__top{
        margin-bottom: .5em;
        @include grid100gap(1em);
        text-align: center;
    }
    &__title{
        font-size: 1.875em;
        line-height: 117%;
        font-weight: 500;
    }
}

.news-modal{
    .modal__dialog{
        padding: 0;
        width: 87.25em;
    }
    &__img{
        border-radius: 1em 1em 0 0;
        @include imgCover(37.4%);
    }
    &__content{
        padding: 2.5em;
    }
    &__text{
        margin-top: 2.25em;
    }
    .close-btn{
        @include center;
        z-index: 3;
        background-color: transparent;
        width: 2.5em;
    }
}

.city-modal{
    .close-btn{
        @include center;
        z-index: 3;
        background-color: transparent;
        width: 2.5em;
        top: 2.5em;
        right: 1.5em;
    }
    .modal__dialog{
        width: 60.5em;
        padding: 2.5em 6em 2.5em 2.5em;
    }
    &__grid{
        display: grid;
        grid-template-columns: calc(100% - 30.5em) 29.5em;
        gap: 1em;
    }
    .modal-form{
        margin: 0;
        padding: 2.5em 1em 4em;
        border-radius: 1em;
        background-color: #fff;
        box-shadow: 2px 2px 20px 0 rgba(78, 80, 79, 0.35);
        &__btn{
            height: 4.375em;
            border-radius: 6.25em;
        }
    }
    &__items{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-top: 1.75em;
    }
    &__item{
        font-size: 1.625em;
        line-height: 115%;
        @include lastM(0 0 .875em);
        &._active{
            border-bottom: 2px solid #005656;
            color: #005656;
            font-weight: 600;
        }
    }
}

.review-modal{
    .modal__dialog{
        width: 60em;
        padding: 2.5em;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .close-btn{
        @include center;
        z-index: 3;
        background-color: transparent;
        width: 2.5em;
        position: static;
    }
}

.review-form{
    margin-top: 2.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    textarea{
        height: 9.5em;
    }
    &__nav{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 1.5em;
    }
    &__btn{
        height: 4.375em;
        width: 25.625em;
        flex-shrink: 0;
        margin-left: 1.5em;
        border-radius: 6.25em;
    }
}

._full{
    grid-column: 1/-1;
}



@media screen and (max-width: 1400px){
    .modal{
        &__dialog {
            width: 26.875em;
        }
    }

    .news-modal{
        .modal__dialog{
            width: 70em;
        }
        &__img{
            @include imgCover(34.9%);
        }
        &__content{
            padding: 1.875em 2.5em;
        }
        &__text{
            margin-top: 2em;
        }
    }
}


@media screen and (max-width: 992px){
    .news-modal{
        &:after{
            display: none;
        }
        .modal__dialog {
            width: 100%;
            height: $vh;
            overflow-y: auto;
            border-radius: 0;
            padding: 0;
        }
        &__img{
            padding-bottom: 39%;
            border-radius: 0;
        }
        &__content{
            padding: 1em .75em;
        }
        &__text{
            margin-top: 1em;
        }
        .close-btn{
            width: 1.5em;
        }
    }

    .city-modal{
        .modal__dialog{
            padding-right: 2.5em;
        }
        &__grid{
            grid-template-columns: 100%;
            gap: 1.5em;
        }
        .modal-form{
            padding: 0;
            box-shadow: unset;
        }
    }

    .review-modal{
        .modal__dialog{
            padding: 2.5em 1.5em;
            width: 26.875em;
        }
    }

    .review-form{
        grid-template-columns: 100%;
        margin-top: 1em;
        &__nav{
            margin: 0;
            @include grid100gap(1em);
        }
        &__btn{
            width: 100%;
            height: 3.125em;
            margin: 0;
        }
        .m-check{
            br{
                display: none;
            }
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        padding: 0;
        &:after{
            display: none;
        }
        &__dialog {
            width: 100%;
            height: $vh;
            overflow-y: auto;
            border-radius: 0;
            padding: 4em 1em;
        }
        &__top{
            gap: .25em;
        }
        &__title{
            font-size: 1.5em;
        }
        .close-btn{
            @include center;
        }
    }

    .modal-form{
        &__btn{
            height: 3.125em;
        }
    }

    .city-modal {
        .modal__dialog {
            padding: 4em 1em;
        }
        .close-btn{
            @include center;
            width: 1.5em;
            top: 1em;
            right: 1em;
        }
        &__items{
            margin-top: 1.5em;
        }
        &__item{
            font-size: 1em;
        }
        .modal-form{
            &__title{
                font-size: 1.5em;
            }
            &__btn{
                height: 3.125em;
            }
        }
    }

    .review-modal{
        .modal__dialog {
            padding: 4em 1em;
        }
        .close-btn{
            position: absolute;
            width: 1.5em;
        }
    }
}
