.h1{
  font-size: 4.625em;
  line-height: 106%;
  font-weight: 600;
  text-transform: uppercase;
}

.h2{
  font-size: 2.875em;
  line-height: 120%;
  letter-spacing: -0.01em;
  font-weight: 600;
}

.h3{
  font-size: 1.875em;
  line-height: 120%;
  font-weight: 500;
}

.p-text,
.text p,
.text26{
  font-size: 1.625em;
  line-height: 120%;
}

.text32{
  font-size: 2em;
  line-height: 116%;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.text-center{
  text-align: center;
}



@media screen and (max-width: 1400px){
  .h1{
    font-size: 3.5em;
    line-height: 120%;
  }

  .h2{
    font-size: 2.5em;
  }

  .text28-laptop{
    font-size: 1.75em;
  }

  .h3,
  .text24-laptop{
    font-size: 1.5em;
  }

  .p-text,
  .text p{
    font-size: 1.25em;
  }
}



@media screen and (max-width: 992px){
  .h1{
    font-size: 2.25em;
  }

  .h2{
    font-size: 2em;
  }

  .text24-tablet{
    font-size: 1.5em;
  }

  .h3,
  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}


@media screen and (max-width: 576px){
  .h1,
  .h2{
    font-size: 1.5em;
  }

  .p-text,
  .h3,
  .text p{
    font-size: 1em;
  }

  .text20-mob{
    font-size: 1.25em;
  }
}
