.why-main{
  padding-bottom: 10em;
  .main__mouse{
    margin-top: 17.5em;
  }
  .main__img{
    img{
      max-height: unset;
    }
  }
}

.advantages{
  padding-top: 0;
  &__grid{
    margin-top: 3.75em;
  }
}

.advantage-card{
  border: 2px solid $blok;
  border-radius: 1em;
  padding: 2.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__icon{
    width: 5em;
    height: 5em;
    @include center;
    border-radius: .6875em;
    border: 1px solid #fff;
    img{
      width: 50%;
    }
  }
  &__content{
    margin-top: 1.5em;
    @include grid100gap(.75em);
  }
  &__title{
    font-size: 2em;
    letter-spacing: -0.01em;
    font-weight: 600;
    &:first-letter{
      color: #c1a875;
      text-transform: uppercase;
      font-size: 1.5em;
    }
  }
}

.work-with{
  &__inner{
    padding: 3em 8.875em;
    background: #272b29;
    border-radius: 1.5em;
    position: relative;
  }
  &__img{
    position: absolute;
    left: 6.25em;
    bottom: 0;
    width: 52.375em;
  }
}

.work-form{
  padding: 2.5em 1.5em;
  background-color: #fff;
  box-shadow: 2px 2px 20px 0 rgba(78, 80, 79, 0.35);
  color: $black;
  border-radius: 1em;
  width: 34.125em;
  max-width: 100%;
  margin-left: auto;
  &__top{
    text-align: center;
    @include grid100gap(.5em);
  }
  &__title{
    font-size: 2em;
    line-height: 116%;
    font-weight: 500;
  }
  &__fields{
    margin-top: 1.5em;
    @include grid100gap(1em);
  }
  &__btn{
    margin-top: 1.5em;
    width: 100%;
    border-radius: 1em;
  }
}


@media screen and (max-width: 1400px){
  .why-main{
    padding-bottom: 8.75em;
    .main__mouse{
      margin-top: 9.25em;
    }
  }

  .advantages{
    &__grid{
      margin-top: 2em;
    }
  }

  .advantage-card{
    padding: 1.5em 1em;
    &__icon{
      width: 4em;
      height: 4em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__desc{
      font-size: 1.125em;
    }
  }

  .work-with{
    &__inner{
      padding: 2.5em 6em;
      overflow: hidden;
    }
    &__img{
      width: 41.5em;
      left: -5em;
    }
  }

  .work-form{
    width: 26.875em;
    &__desc{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .why-main{
    padding: 9em 0;
  }

  .advantages{
    padding-top: 2.5em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
  }

  .work-with{
    &__inner{
      padding: 0;
      background-color: transparent;
    }
    &__img{
      display: none;
    }
  }

  .work-form{
    width: 100%;
    border-radius: 1em;
    padding: 1.5em .75em;
    &__top{
      gap: .25em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__desc{
      font-size: 1em;
    }
    &__btn{
      height: 3.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
