.vacancy-main{
  .main__bottom{
    max-width: 75.5em;
    gap: 6.25em;
  }
  .main__mouse{
    margin-top: 14.0625em;
  }
  @media screen and (min-width: 993px){
    .main__img img{
      max-height: unset;
    }
  }
}


@media screen and (max-width: 1400px){
  .vacancy-main{
    padding-bottom: 6em;
    margin-bottom: 3.75em;
    .main__btn{
      margin-top: 1.5em;
    }
    .main__mouse{
      margin-top: 7.5em;
    }
    .main__bottom{
      gap: 3em;
    }
    @media screen and (min-width: 993px){
      .main__img img{
        height: 100%;
      }
    }
  }

  ._round--vacancy{
    background-image: url(../img/ellipse2.svg);
    width: 100%;
    height: 32.375em;
    background-color: transparent;
    @include bgImgSize(100%);
    filter: blur(120px);
    top: 52.5em;
    border-radius: unset;
  }
}


@media screen and (max-width: 992px){
  .vacancy-main{
    padding-bottom: 2.5em;
    margin-bottom: 0;
    .main__btn{
      margin-top: 1em;
    }
    .main__bottom{
      gap: .75em;
    }
  }

  ._round--vacancy{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .vacancy-main{
    background: transparent;
    .main__bottom{
      margin-top: 10em;
    }
    .main{
      &__img{
        height: 30em;
      }
    }
  }
}
