@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 4em;
    height: 3.3125em;
    border-radius: 3.75em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.25em;
    }
}

.m-btn-white-outline{
    border: 1px solid #757575;
    color: #fff;
    &._type2{
        border-color: #fff;
    }
    &:hover{
        border-color: #fff;
        background-color: #fff;
        color: $black;
    }
}

.m-btn-black-outline{
    border: 1px solid $black;
    color: $black;
    &:hover{
        border-color: $black;
        background-color: $black;
        color: #fff;
    }
}

.m-btn-green{
    background: #37b04d;
    color: #fff;
    &:hover{
        background: darken(#37b04d,10%);
        color: #fff;
    }
}

.m-btn-black{
    background: #000;
    color: #fff;
    &:hover{
        background: darken(#37b04d,10%);
        color: #fff;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 2.625em;
        padding: 0 1em;
        span{
            font-size: 1em;
        }
    }
}
