.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .9375em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: $primary;
    }
  }
  input,
  textarea{
    border-radius: 1.2em;
    border: 1px solid $block;
    padding: 0 1em;
    height: 3em;
    width: 100%;
    font-size: 1.25em;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: $block;
    }
    &:focus {
      border-color: #9CA0A0;
      box-shadow: 0 .25em .4375em 0 rgba(47, 63, 67, 0.03), 0 .25em .3125em 0 rgba(0, 0, 0, 0.02) inset;
    }
    &:disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      color: rgba(#000,.4);
    }
  }
  textarea{
    padding-top: 1em;
    padding-bottom: 1em;
    height: 5.75em;
    resize: none;
  }
}


.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 1.875em;
    min-height: 1.25em;
    span{
      font-size: 1.125em;
      line-height: 120%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      border: 1px solid #000;
      width: 1.25em;
      height: 1.25em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-size: .625em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .25em;
      background-image: url(../img/check2.svg);
    }
  }
}


@media screen and (max-width: 992px){
  .m-check{
    label span{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .fg{
    input,
    textarea{
      font-size: 1em;
      padding: 0 1.25em;
      height: 3.125em;
    }
    textarea{
      padding-top: 1em;
      padding-bottom: 1em;
      height: 7em;
    }
  }

  .m-check{
    label span{
      font-size: .875em;
    }
  }
}
