.footer{
  padding: 5em 0 3.75em;
  color: #FFFFFF;
  position: relative;
  z-index: 3;
  &__inner{
    position: relative;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2.5em 0;
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
  }
  &__logo{
    width: 16.75em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  .nav{
    position: static;
    width: 50em;
    transform: unset;
  }
  &__bottom{
    margin-top: 3.5em;
    display: flex;
    align-items: center;
    min-height: 3.3125em;
  }
  &__c{
    font-size: 1.25em;
  }
  &__items{
    height: 3.3125em;
    display: flex;
    align-items: center;
    position: absolute;
    right: 0;
    bottom: 0;
  }
  &__item{
    display: flex;
    align-items: center;
    margin-left: 6em;
    &:hover{
      color: #fff;
      opacity: .8;
    }
    img{
      width: 1.875em;
      margin-right: .75em;
    }
    span{
      font-size: 1.25em;
    }
  }
  &__btn{
    padding: 0 4em;
    margin-left: 5.25em;
  }
}


@media screen and (max-width: 1400px){
  .footer{
    &__logo{
      width: 10.625em;
    }
    .nav{
      width: 39em;
    }
    &__bottom{
      margin-top: 2em;
    }
    &__btn{
      margin-left: 2.5em;
      padding: 0 1.5em;
      height: 2.3125em;
      span{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2.5em 0 .5em;
    &__top{
      border: none;
      flex-direction: column;
      padding: 0;
    }
    &__logo{
      width: 6em;
    }
    &__items{
      position: static;
    }
    &__logo{
      margin-right: auto;
    }
    .nav{
      margin-top: 1.5em;
      &__link{
        font-size: 1.125em;
        @include lastM(0 0 .67em);
        padding-bottom: 0;
        border-bottom: none;
      }
    }
    &__right{
      width: 100%;
      margin-top: 1.5em;
    }
    &__items{
      flex-direction: column;
      width: 100%;
      position: static;
      height: unset;
    }
    &__item{
      width: 100%;
      justify-content: center;
      margin: 0 0 .75em;
      padding-bottom: .25em;
      border-bottom: 1px solid #fff;
      img{
        width: 1.5em;
      }
      span{
        font-size: 1em;
      }
    }
    .socials{
      width: 100%;
      padding-bottom: .25em;
      border-bottom: 1px solid #fff;
      justify-content: center;
      &__item{
        @include lastM(0 .5em 0 0);
        width: 1.5em;
        height: 1.5em;
      }
    }
    &__btn{
      position: absolute;
      right: 0;
      top: .25em;
    }
    &__bottom{
      margin-top: 1.5em;
      justify-content: center;
      min-height: unset;
    }
    &__c{
      font-size: 1em;
    }
  }
}


