.reviews-slider{
  margin-top: 3.5em;
  display: flex;
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
  }
  .slider{
    &__wrap{
      margin-top: -3em;
      padding-top: 3em;
    }
  }
}

.review-card{
  padding: 4.75em 2.5em 2.5em;
  background-color: #0e2017;
  border-radius: 1em;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  &__img{
    position: absolute;
    top: -3em;
    left: 2.5em;
    box-shadow: 2px 2px 20px 0 rgba(78, 80, 79, 0.35);
    border-radius: .5em;
    width: 6.25em;
    height: 6.25em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: inherit;
    }
  }
  &__content{
    min-height: 14.5em;
    padding-bottom: 2.5em;
    border-bottom: 1px solid #fff;
    flex-grow: 1;
  }
  &__desc{
    font-size: 1.25em;
    line-height: 120%;
    @include truncate(9);
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__info{
    @include grid100gap(.25em);
  }
  &__stars{
    height: 1.5em;
    img{
      display: block;
      height: 100%;
    }
  }
}

.cooperate{
  &__grid{
    margin-top: 3.75em;
  }
}

.cooperate-card{
  padding: 3.125em 1.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &:before{
    background: rgba(255, 255, 255, 0.1);
  }
  &__icon{
    width: 5em;
    height: 5em;
    @include center;
    border-radius: .6875em;
    border: 1px solid #fff;
    img{
      width: 50%;
    }
  }
  &__content{
    margin-top: 1.625em;
    @include grid100gap(.8125em);
  }
}

.partners-slider{
  margin-top: 3.75em;
  .swiper{
    @media screen and (min-width: 1401px){
      &-container{
        width: calc(100% + 4.375em);
      }
      &-slide{
        padding-right: 4.375em;
      }
    }
    &-slide{
      width: 25%;
    }
  }
}

.partner-item{
  display: flex;
  align-items: center;
  height: 100%;
  img{
    width: 100%;
    display: block;
  }
}


@media screen and (max-width: 1400px){
  .reviews-slider{
    margin-top: 3em;
    .slider{
      &__wrap{
        margin-top: 0;
        padding-top: 2.5em;
      }
    }
  }

  .review-card{
    padding: 4em 1.5em 1.5em;
    &__img{
      top: -2.5em;
      left: 1.5em;
      width: 5em;
      height: 5em;
    }
    &__content{
      min-height: 15.25em;
      padding-bottom: 1.5em;
    }
    &__desc{
      font-size: 1.125em;
      -webkit-line-clamp: 12;
    }
    &__bottom{
      padding-top: 1em;
      display: block;
      position: relative;
    }
    &__info{
      gap: .5em;
    }
    .h3{
      font-size: 1.5em;
    }
    &__city{
      font-size: 1.25em;
    }
    &__stars{
      position: absolute;
      right: 0;
      bottom: 0;
      height: 1.25em;
    }
  }

  .cooperate{
    &__grid{
      margin-top: 2em;
    }
  }

  .cooperate-card{
    padding: 2.5em 1em;
    &__icon{
      width: 4em;
      height: 4em;
    }
    &__content{
      margin-top: 1em;
      gap: .5em;
    }
    &__desc{
      font-size: 1em;
    }
  }

  .partners-slider{
    margin-top: 3em;
  }
}


@media screen and (max-width: 992px){
  .reviews-slider{
    margin-top: 1.5em;
    display: block;
    .swiper-slide{
      width: 50%;
    }
  }

  .cooperate{
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
    }
  }

  .cooperate-card{
    flex-direction: row;
    padding: 1em;
    text-align: left;
    &__icon{
      flex-shrink: 0;
      width: 3em;
      height: 3em;
    }
    &__content{
      margin-left: .75em;
      margin-top: 0;
      gap: .25em;
    }
    &__title{
      font-size: 1.25em;
    }
    &__desc{
      font-size: .875em;
      line-height: 100%;
      br{
        display: none;
      }
    }
  }

  .partners-slider{
    margin-top: 1.5em;
  }
}


@media screen and (max-width: 576px){
  .reviews-slider{
    .swiper-slide{
      width: 100%;
    }
  }

  .cooperate{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .partners-slider{
    .swiper-slide{
      width: 50%;
    }
  }
}
