._ellipse{
  position: absolute;
  filter: blur(564.7000122070312px);
}

.main{
  padding: 21.375em 0 10em;
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      display: block;
      max-height: 100%;
      object-fit: cover;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    max-width: 81.25em;
    width: 100%;
    margin: 0 auto;
    position: relative;
    z-index: 4;
  }
  &__btn{
    margin-top: 3.75em;
    width: 25.375em;
    max-width: 100%;
  }
  &__mouse{
    width: 2.5em;
    margin-top: 8.75em;
  }
  &__bottom{
    margin: 10em auto 0;
    max-width: 73.75em;
    @include grid100gap(3.125em);
  }
  &__ellipse{
    top: 13em;
    left: 50%;
    transform: translateX(-50%);
  }
  &__desc{
    max-width: 69.5em;
    width: 100%;
    margin: 3.75em auto 0;
  }
  @media screen and (min-width: 577px){
    .h2 br{
      display: none;
    }
  }
}

._round{
  position: absolute;
  width: 340em;
  height: 364em;
  border-radius: 100%;
  background: linear-gradient(180deg, #132119 0%, #02ad4b 100%);
  z-index: 2;
  top: 47em;
  left: 50%;
  transform: translateX(-50%);
  filter: blur(500px);
  pointer-events: none;
}

._z3{
  position: relative;
  z-index: 3;
}

.history{
  padding: 0 0 5em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 44.25em) 43em;
    gap: 1.25em;
    margin-top: 3.75em;
  }
}

.history-card,
.cooperate-card{
  position: relative;
  border-radius: 1em;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    border-radius: inherit;
    width: 100%;
    height: 100%;
    z-index: -1;
    border: 1px solid #fff;
  }
}

.history-card{
  padding: 2.5em;
  &:before{
    backdrop-filter: blur(45px);
    background: $blok;
  }
  @media screen and (min-width: 1401px){
    &:nth-child(3){
      .history-card__text{
        p{
          @include lastM(0 0 1.38em);
        }
      }
    }
  }
  @media screen and (min-width: 993px){
    &:nth-child(3){
      grid-row: 1/3;
      grid-column: 2/3;
    }
  }
  &__text{
    margin-top: 1.5em;
  }
}

.text{
  p{
    @include lastM(0 0 .62em);
  }
}



@media screen and (max-width: 1400px){
  .main{
    padding: 16.5em 0 6.625em;
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__btn{
      margin-top: 2.25em;
      width: 22.5em;
    }
    &__mouse{
      margin-top: 7.5em;
    }
    &__bottom{
      margin-top: 7.5em;
      gap: 2em;
    }
    &__desc{
      margin-top: 1.5em;
    }
  }

  .history{
    padding-top: 3.375em;
    &__grid{
      grid-template-columns: calc(100% - 28.4375em - 1.25em) 28.4375em;
      margin-top: 2em;
    }
  }

  .history-card{
    padding: 1.5em;
  }

  .text{
    p{
      @include lastM(0 0 .6em);
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 6.375em 0 2.75em;
    &__btn{
      margin-top: 1em;
      border-color: #fff;
    }
    &__mouse{
      display: none;
    }
    &__img{
      height: 100%;
      img{
        height: 100%;
        object-fit: cover;
      }
    }
    &__bottom{
      margin-top: 5em;
      gap: .75em;
    }
  }

  .history{
    padding: 2.375em 0 2.5em;
    &__grid{
      grid-template-columns: 100%;
      gap: .75em;
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__img{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
  }

  .history-card{
    &__text{
      margin-top: .75em;
    }
  }

  .text{
    p{
      @include lastM(0 0 .75em);
    }
  }
}
