@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.5em;
    height: 2.5em;
    @include lastM(0 4.375em 0 0);
    @include center;
    &:hover{
      opacity: .8;
    }
    svg{
      width: 100%;
    }
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}


@media screen and (max-width: 1400px){
  .socials{
    &__item{
      @include lastM(0 1.5em 0 0);
    }
  }
}
