.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      padding-right: 1.25em;
    }
    &-scrollbar{
      overflow: hidden;
      height: .75em;
      border: 1px solid #fff;
      margin-top: .75em;
      padding: .1875em;
      &-drag{
        background-color: #73FF8C;
      }
      @media screen and (min-width: 993px){
        display: none;
      }
    }
    &-nav{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 3.5em;
      display: flex;
      align-items: center;
    }
    &-button{
      position: static;
      margin: 0 0 0 3em;
      width: 2.5em;
      height: 2.5em;
      svg{
        width: 100%;
      }
    }
  }
}

.m-section{
  padding: 5em 0;
}

.conditions{
  &__items{
    display: flex;
    justify-content: space-between;
    margin-top: 3.75em;
    padding-bottom: 2.5em;
    border-bottom: 1px solid #fff;
  }
  &__item{
    display: flex;
    align-items: center;
    min-height: 5.125em;
    img{
      margin-right: 1em;
      width: 2.5em;
    }
  }
  &__bottom{
    margin-top: 3.75em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__info{
    @include grid100gap(1.5em);
  }
  &__btn{
    flex-shrink: 0;
    width: 25.375em;
    margin-left: 10.375em;
  }
}

.vacancies{
  &__top{
    @include grid100gap(1em);
  }
  &__nav{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    border-radius: 1em;
    padding: 0 5em;
    height: 3em;
  }
}

.vacancies-slider{
  margin-top: 3.5em;
  @media screen and (min-width: 993px){
    .swiper{
      &-container{
        width: 100%;
      }
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
        gap: 1.25em;
      }
      &-slide{
        padding-right: 0;
      }
      &-scrollbar{
        display: none;
      }
    }
  }
}

.vacancy-card{
  height: 100%;
  padding: 2.5em;
  background-color: #fff;
  border-radius: 1em;
  color: $dark;
  display: flex;
  flex-direction: column;
  &__top{
    @include grid100gap(.5em);
  }
  &__group{
    margin-top: 1.5em;
  }
  &__subtitle{
    display: flex;
    align-items: center;
    font-weight: 500;
    &:before{
      content: '';
      flex-shrink: 0;
      width: 1.25em;
      height: 1.25em;
      background-image: url(../img/check.svg);
      @include bgImgSize(100%);
      margin-right: .5em;
    }
  }
  &__list{
    margin-top: .5em;
    @include grid100gap(.25em);
    li{
      font-size: 1.25em;
      line-height: 120%;
    }
  }
  &__nav{
    padding-top: 1.25em;
    margin-top: auto;
    .m-btn{
      width: 100%;
      height: 3em;
      border-radius: 1em;
    }
  }
}

.team-slider{
  margin-top: 3em;
  .swiper{
    &-slide{
      width: 25%;
    }
  }
}

.team-card{
  &__img{
    @include imgCover(84%);
    border-radius: 1em;
    margin-bottom: 1.5em;
  }
  &__name{
    font-size: 2.5em;
    line-height: 110%;
    font-weight: 500;
  }
  &__desc,
  &__desc2{
    font-size: 1.25em;
    line-height: 120%;
    margin-top: .2em;
  }
  &__desc2{
    color: #d2d2d2;
    a{
      text-decoration: underline;
    }
  }
}


@media screen and (max-width: 1400px){
  .slider{
    .swiper{
      &-nav{
        margin-bottom: 2.5em;
      }
      &-button{
        width: 2em;
        height: 2em;
        margin: 0 0 0 2.5em;
      }
    }
  }

  .conditions{
    &__items{
      margin-top: 3em;
      padding-bottom: 2em;
    }
    &__item{
      min-height: 2em;
      img{
        width: 2em;
      }
    }
    &__bottom{
      margin-top: 2em;
    }
    &__info{
      gap: 1em;
    }
    &__btn{
      width: 22.5em;
      margin-left: 1.5em;
    }
  }

  .vacancies{
    &__top{
      gap: .5em;
      .h3{
        font-weight: 400;
      }
    }
    &__nav{
      margin-top: 1.5em;
    }
  }

  .vacancies-slider{
    margin-top: 2em;
  }

  .vacancy-card{
    padding: 1.5em;
    &__group{
      margin-top: 1em;
    }
    &__list{
      li{
        font-size: 1.125em;
      }
    }
  }

  .team-slider{
    margin-top: 2em;
  }

  .team-card{
    &__img{
      margin-bottom: 1em;
    }
    &__name{
      font-size: 1.75em;
    }
    &__desc,
    &__desc2{
      font-size: 1em;
    }
    &__desc{
      margin-top: 0;
    }
    &__desc2{
      margin-top: .25em;
    }
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 2.5em 0;
  }

  .slider{
    .swiper-nav{
      display: none;
    }
  }

  .conditions{
    &__items{
      margin-top: 1.5em;
      padding-bottom: 1em;
    }
    &__item{
      min-height: unset;
      img{
        width: 1.5em;
        margin-right: .75em;
      }
    }
    &__bottom{
      margin-top: 1em;
      display: block;
    }
    &__info{
      gap: .75em;
    }
    &__btn{
      width: 100%;
      margin: .75em 0 0;
    }
  }

  .vacancies{
    overflow: hidden;
    &__top{
      gap: .25em;
    }
    &__btn{
      height: 2.625em;
    }
  }

  .vacancies-slider{
    margin-top: 1.5em;
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        width: 50%;
      }
    }
  }

  .team-slider{
    margin-top: 1.5em;
  }

  .team-card{
    &__img{
      margin-bottom: .75em;
    }
    &__name{
      font-size: 1em;
    }
    &__desc,
    &__desc2{
      font-size: .875em;
      margin-top: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .conditions {
    &__items {
      @include grid100gap(.75em);
    }
  }

  .vacancies{
    &__top{
      .h3{
        max-width: 11.5em;
      }
    }
    &__btn{
      width: 100%;
    }
  }

  .vacancies-slider{
    .swiper-slide{
      width: 100%;
    }
  }

  .team-slider{
    .swiper-slide{
      width: 50%;
    }
  }
}
