.index-bottom{
  padding: 2.75em 0 4.375em;
  margin-top: 2.25em;
  @include grid100gap(11em);
  background-image: url(../img/blog-bg.webp);
  background-size: cover;
}

.blog-slider{
  margin-top: 3.75em;
  .swiper{
    &-slide{
      width: 50%;
    }
    &-button{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 5em;
      margin: 0;
      &-prev{
        left: -2.5em;
      }
      &-next{
        right: -2.5em;
      }
    }
  }
}

.blog-card{
  position: relative;
  color: $text2;
  &._green{
    .blog-card__title{
      background: linear-gradient(180deg, #093211 0%, #0a7f3b 100%);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
  &__img{
    border-radius: 1em;
    @include imgCover(49.5%);
    ._view2{
      display: none;
    }
  }
  &__content{
    position: absolute;
    bottom: 0;
    left: 0;
    border-radius: 0 0 1em 1em;
    padding: 1.75em 2.5em;
    background: rgba(255, 255, 255, 0.6);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    max-width: 14em;
  }
  &__link{
    font-size: 1.25em;
    line-height: 115%;
    white-space: nowrap;
  }
}

.contacts{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 53.375em) 52.375em;
    gap: 1em;
  }
  &__col1{
    padding-top: 3.375em;
  }
  &__top{
    @include grid100gap(1.5em);
  }
  &__items{
    margin-top: 1.5em;
    @include grid100gap(1em);
  }
  &__item{
    min-height: 5.25em;
    display: flex;
    align-items: center;
    img{
      width: 3.5em;
      margin-right: 1.5em;
    }
    @media screen and (min-width: 1401px){
      br{
        display: none;
      }
    }
    @media screen and (min-width: 993px){
      &._type2{
        img{
          width: 2.8125em;
          margin-right: 1em;
        }
      }
    }
  }
  &__nav{
    display: flex;
    margin-top: .75em;
  }
  &__btn{
    width: 22.8125em;
    padding: 0;
    max-width: 100%;
  }
}

.map{
  height: 51em;
  border-radius: 1em;
  overflow: hidden;
}


@media screen and (max-width: 1400px){
  .index-bottom{
    padding: 5.5em 0;
    gap: 5.3125em;
  }

  .blog-slider{
    margin-top: 2em;
    .swiper{
      &-button{
        width: 3.75em;
        &-prev{
          left: -1.875em;
        }
        &-next{
          right: -1.875em;
        }
      }
    }
  }

  .blog-card{
    &__content{
      padding: .9375em 2.5em;
    }
  }

  .contacts{
    &__grid{
      grid-template-columns: calc(100% - 34.375em) 33.125em;
      gap: 1.5em 1.25em;
    }
    &__col1{
      padding-top: 0;
    }
    &__top{
      gap: .5em;
    }
    &__items{
      margin-top: 1em;
      gap: 0;
    }
    &__item{
      max-width: 25em;
      span{
        font-size: 1.625em;
      }
    }
    &__nav{
      margin-top: 1em;
    }
  }

  .map{
    height: 42.3125em;
  }
}


@media screen and (max-width: 992px){
  .index-bottom{
    padding: 2.5em 0;
    gap: 5em;
  }

  .blog-slider{
    margin-top: 1.5em;
    .swiper{
      &-button{
        display: none;
      }
      &-container{
        width: 100%;
      }
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .25em));
        gap: .5em;
        transform: unset !important;
      }
      &-slide{
        width: 100%;
        padding: 0;
        &:nth-child(2)~.swiper-slide{
          display: none;
        }
      }
    }
  }

  .blog-card{
    &__img{
      padding-bottom: 78.3%;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__content{
      padding: .75em 1em;
      flex-direction: column;
      text-align: center;
    }
    &__title{
      max-width: unset;
      font-size: 1em;
      line-height: 110%;
    }
    &__link{
      font-size: 1em;
      text-decoration: underline;
      margin-top: .75em;
    }
  }

  .contacts{
    &__grid{
      grid-template-columns: 100%;
    }
    &__top{
      gap: .25em;
      .h3{
        max-width: 13em;
      }
    }
    &__items{
      gap: 1em;
    }
    &__item{
      min-height: 3em;
      img{
        width: 3em;
        margin-right: .75em;
      }
      span{
        font-size: 1.25em;
      }
    }
  }

  .map{
    height: 22em;
  }
}


@media screen and (max-width: 576px){
  .blog-slider{
    .swiper{
      &-wrapper{
        grid-template-columns: 100%;
      }
    }
  }

  .contacts{
    &__nav{
      margin-top: 1.5em;
    }
    &__btn{
      width: 100%;
    }
  }

  .map{
    height: 15em;
  }
}
