.history-main{
  .main__inner{
    max-width: 94em;
  }
  .main__desc{
    max-width: 87.25em;
  }
  .main__img{
    img{
      max-height: unset;
    }
  }
  .main__mouse{
    margin-top: 5.375em;
  }
}


@media screen and (max-width: 1400px){
  .history-main{
    padding-bottom: 11.25em;
    margin-bottom: 4em;
    .main__mouse{
      margin-top: 7.5em;
    }
  }
}


@media screen and (max-width: 992px){
  .history-main{
    padding-bottom: .625em;
    margin-bottom: 1.25em;
    .main__desc{
      margin-top: 1em;
    }
  }
}


