.contacts-main{
  padding-bottom: 13.375em;
  .main__img{
    img{
      max-height: unset;
    }
  }
}

.contacts2{
  padding-top: 0;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 15.25em;
    margin-bottom: 1.5em;
  }
  .map{
    height: 71.25em;
    margin-top: 3.75em;
  }
  .contacts__items{
    display: flex;
    justify-content: space-between;
    margin-top: 2.5em;
  }
  .contacts__nav{
    justify-content: center;
    margin-top: 2.5em;
  }
}

.city-select{
  display: flex;
  align-items: center;
  transition: all .3s;
  &:hover{
    cursor: pointer;
    opacity: .8;
  }
  img{
    width: 1.875em;
  }
  &__title{
    margin: 0 .75em;
    font-weight: 600;
    span{
      font-size: 1.5em;
      line-height: 120%;
    }
  }
}



@media screen and (max-width: 1400px){
  .contacts-main{
    padding-bottom: 8.75em;
  }

  .contacts2{
    &__top{
      padding-right: 0;
      margin-bottom: .5em;
    }
    .map{
      height: 57.5em;
      margin-top: 3em;
    }
    .contacts__items{
      display: grid;
      grid-template-columns: repeat(4,calc(25% - 3em / 4));
      gap: 1em;
      margin-top: 1em;
    }
    .contacts__item{
      &._type2{
        img{
          width: 2.5em;
        }
      }
      img{
        width: 3em;
      }
      span{
        font-size: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .contacts-main{
    padding: 10.625em 0;
  }

  .contacts2{
    padding-top: 2.5em;
    &__top{
      margin-bottom: .5em;
    }
    .contacts__items{
      grid-template-columns: 100%;
    }
    .contacts__item{
      &._type2{
        img{
          width: 3em;
        }
      }
    }
    .map{
      height: 25em;
      margin-top: 1.5em;
    }
  }

  .city-select{
    padding-right: 2.625em;
    position: relative;
    .city-select__icon{
      width: 1em;
    }
    &__arrow{
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
    }
    &__title{
      margin: 0 0 0 .5em;
      span{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .contacts2{
    .map{
      height: 15em;
    }
  }
}
